<template>
  <div id="create">
    <h1 @click="publish">创建博客</h1>

    <el-form class="blogForm">
      <el-form-item class="title-item">
        <el-input
          v-model="title_text"
          placeholder="输入标题（限30个字）"
          type="input"
          oninput="if(value.length>30)value=value.slice(0,30)"
        ></el-input>
      </el-form-item>

      <el-form-item class="title-item">
        <el-input
          v-model="tag_name"
          placeholder="选择标签"
          type="input"
          oninput="if(value.length>30)value=value.slice(0,30)"
        ></el-input>
      </el-form-item>

      <el-form-item class="tag-item">
        <el-tag
          :key="tag"
          v-for="tag in dynamicTags"
          closable
          :disable-transitions="false"
          @close="handleClose(tag)"
        >{{tag}}</el-tag>
        <el-input
          class="input-new-tag"
          v-if="inputVisible"
          v-model="inputValue"
          ref="saveTagInput"
          size="small"
          @keyup.enter.native="handleInputConfirm"
          @blur="handleInputConfirm"
        ></el-input>
        <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
      </el-form-item>

      <el-form-item class="article-item">
        <el-input v-model="article_text" placeholder="正文（至少20个字）" type="textarea" rows="15"></el-input>
      </el-form-item>
    </el-form>
    <el-button @click="publish">发布</el-button>
    <el-button @click="save">保存</el-button>
    <el-button @click="cancel">取消</el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title_text: "",
      article_text: "",
      //element ui -- tag
      dynamicTags: ["Java", "Python", "Go", "C++", "C#", "JavaScript"],
      inputVisible: false,
      inputValue: ""
    };
  },
  methods: {
    publish() {
      axios
        .post("/deliver", {
          user_id: "8",     
          title: this.title_text,
          content:this.article_text,
          tag_id:"5",
          category_id:"3",
          tag_name: this.dynamicTags[1] //233
        })
        .then(res => {
          console.log(
            "info: ",
            this.title_text,
            this.article_text,
            this.dynamicTags[0]
          );
          console.log("data", data);
          if (res.data.status == "200") {
            this.$message.success("发布成功");
            setTimeout(() => {
              this.$router.push("/detail");
            }, 1000);
          } else {
            this.$message.error(res.data.msg);
          }
        });
    },
    save() {
      this.$message.success("保存成功");
    },
    cancel() {
      this.$confirm("确定取消?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$message({
          type: "success",
          message: "已取消"
        });
        this.$router.push("/");
      });
    },

    //element ui -- tag
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
    },

    //input tag
    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.dynamicTags.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = "";
    }
  }
};
</script>

<style scoped>
/* common */
#create {
  padding-bottom: 100px;
}
#create h1 {
  line-height: 3;
}
@media (max-width: 500px) {
  #create {
    width: 80vw;
  }
}
.tag-item {
  text-align: left;
}

/* diy element ui */
.el-textarea__inner {
  padding: 10px 14px;
  font-family: "Microsoft Ya Hei";
}

/* element ui -- tag */
.el-tag {
  margin-right: 10px;
}
.button-new-tag {
  margin-right: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-right: 10px;
  vertical-align: bottom;
}
</style>



