<template>
  <div id="info">
    <ul class="option">
      <li class="top-line">
        <div class="cell cell-left">头像</div>
        <div class="cell cell-info">
          <i class="icon-ali-avatar"></i>
          <el-upload
            name="avatar"
            class="avatar-uploader"
            :action="updateUrl"
            :data="itemForm"
            :before-upload="beforeAvatarUpload"
            :on-success="handleAvatarSuccess"
            ref="newupload"
          >
            <el-button type="file" class="avatar-btn">更改头像</el-button>
          </el-upload>
        </div>
        <!-- <div class="cell cell-btn">
          <el-button></el-button>
        </div>-->
      </li>
      <li class="id-line">
        <div class="cell cell-left">工号</div>
        <div class="cell cell-info">S001</div>
        <!-- <div class="cell cell-btn">
          <el-button></el-button>
        </div>-->
      </li>
      <li class="nick-line">
        <div class="cell cell-left">昵称</div>
        <div class="cell cell-info">
          <el-input
            v-model="nick_name"
            placeholder
            type="input"
            oninput="if(value.length>10)value=value.slice(0,10)"
            clearable
          >Takai</el-input>
        </div>
      </li>
      <li class="intro-line">
        <div class="cell cell-left">个人简介</div>
        <div class="cell cell-info">
          <el-input type="textarea" :rows="4" placeholder="请输入内容" v-model="intro_text"></el-input>
        </div>
        <!-- <div class="cell cell-btn">
          <el-button></el-button>
        </div>-->
      </li>
      <li>
        <div class="cell">
          <el-button @click="saveInfo" type="success">保存</el-button>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      nick_name: "Takai",
      intro_text: "balabala巴拉巴拉balabala巴拉巴拉balabala巴拉巴拉"
    };
  },
  methods: {
    saveInfo() {
      this.$message.success("保存成功");
    }
  }
};
</script>

<style>
/* common */
#info {
  padding-top: 20px;
  padding-bottom: 90px;
}
@media (max-width: 500px) {
  #info {
    width: 90vw;
  }
}
ul.option {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
ul.option li {
  display: flex;
  justify-content: center;
  align-items: center;
}

.option .cell {
  padding: 20px 20px;
  font-size: 14px;
  text-align: left;
  display: flex;
  align-items: center;
}
.option .cell i.icon-ali-avatar {
  font-size: 28px;
}
.option .cell-left {
  color: #c6c6c6;
  width: 100px;
}
.option .cell-info {
  color: #666;
  width: 300px;
}

/* diy element ui */
.el-button {
  border-radius: 20px;
}
.el-button:focus{
  color: #fff;
  border-color: #85ce61;
  outline: none;
}

.avatar-btn {
  margin-left: 30px;
}

.el-textarea__inner {
  padding: 10px 14px;
  font-size: 14px;
  font-family: "Microsoft Ya Hei";
  color: inherit;
  resize: none;
}
</style>



