<template>
  <div id="admin-edit">
    <h1>管理用户</h1>
    <div class="control-btns">
      <el-button round @click="toggleSelection()">全选</el-button>
      <el-button round @click="confirm('封禁')" type="danger">封禁</el-button>
      <el-button round @click="confirm('正常')" type="primary">解封</el-button>
      <el-button class="add-btn" round @click="enterUserInfo()">录入</el-button>
    </div>
    <el-table
      class="user-list"
      ref="multipleTable"
      :data="tableUser"
      tooltip-effect="dark"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column label="工号" width>
        <template slot-scope="scope">{{ scope.row.id }}</template>
      </el-table-column>
      <el-table-column prop="name" label="姓名" width></el-table-column>
      <el-table-column
        prop="state"
        label="状态"
        width="100"
        :filters="[{ text: '正常', value: '正常' }, { text: '封禁', value: '封禁' }]"
        :filter-method="filterTag"
        filter-placement="bottom-end"
      >
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.state === '正常' ? 'primary' : 'danger'"
            close-transition
          >{{scope.row.state}}</el-tag>
        </template>
      </el-table-column>
    </el-table>

    <div class="pagi-wrapper" v-if="pagenavshow">
      <el-pagination class="pagi" background layout="prev, pager, next" :total="50"></el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      sum: 100,
      curpage: "1",
      tableUser: [
        {
          id: "S001",
          name: "十三",
          state: "正常"
        },
        {
          id: "S002",
          name: "十三",
          state: "封禁"
        },
        {
          id: "S004",
          name: "十三",
          state: "正常"
        },
        {
          id: "S005",
          name: "十三",
          state: "正常"
        },
        {
          id: "S006",
          name: "十三",
          state: "正常"
        },
        {
          id: "S007",
          name: "十三",
          state: "正常"
        },
        {
          id: "S008",
          name: "十三",
          state: "正常"
        },
        {
          id: "S009",
          name: "十三",
          state: "正常"
        },
        {
          id: "S010",
          name: "十三",
          state: "正常"
        }
      ],
      multipleSelection: []
    };
  },

  methods: {
    toggleSelection() {
      this.$refs.multipleTable.toggleAllSelection();
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },

    pageclick: function(item) {
      this.curpage = item;
      console.log(this.curpage);
    },

    enterUserInfo() {
      this.$prompt("输入员工工号", "录入", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputPattern: /^[a-zA-Z][0-9]{3}$/,
        inputErrorMessage: "工号格式不正确"
      })
        .then(({ value }) => {
          var user = {
            id: value,
            name: value,
            state: "正常"
          };
          this.tableUser.push(user);
          this.$message({
            type: "success",
            message: "录入的工号是: " + value
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消录入"
          });
        });
    },

    addRequest() {
      axios
        .post("/setUser", {
          work_id: tableUser.id,
          name: work_id
        })
        .then(res => {
          console.log("info: ");
          console.log("data", data);
          if (res.data.status == "200") {
            this.$message.success("录入成功");
            setTimeout(() => {
              this.$router.push("/setUser");
            }, 1000);
          } else {
            this.$message.error(res.data.msg);
          }
        });
    },

    confirm(state) {
      if (this.multipleSelection.length === 0) {
        this.$message.warning("请至少勾选一项，再进行操作");
      } else {
        let confirmMsg = state === "封禁" ? "确认封禁?" : "确认解封?",
          successMsg = state === "封禁" ? "封禁成功" : "解封成功",
          cancelMsg = state === "封禁" ? "已取消封禁" : "已取消解封";
        this.$confirm(confirmMsg, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            let selects = this.multipleSelection;
            for (let i = 0; i < selects.length; i++) {
              selects[i].state = state;
            }
            this.$message({
              type: "success",
              message: successMsg
            });
            this.$refs.multipleTable.clearSelection();
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: cancelMsg
            });
          });
      }
    },

    formatter(row, column) {
      return row.address;
    },
    filterTag(value, row) {
      return row.tag === value;
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    }
  },
  computed: {
    pagenavshow: function() {
      if (this.sum == 1) {
        return false;
      } else {
        return true;
      }
    },

    shownot: function() {
      if (this.sum < 12) {
        return true;
      } else {
        return false;
      }
    },
    firstsum: function() {
      if (this.curpage < 6) {
        return [1, 2, 3, 4, 5, 6, 7];
      } else {
        return 1;
      }
    },
    middlesum: function() {
      var cpage = this.curpage;
      if (this.curpage < 6 || this.curpage > this.sum - 4) {
        return false;
      } else {
        return [cpage - 2, cpage - 1, cpage, cpage + 1, cpage + 2];
      }
    },
    lastsum: function() {
      if (this.curpage < this.sum - 3) {
        return [this.sum];
      } else {
        return [
          this.sum - 5,
          this.sum - 4,
          this.sum - 3,
          this.sum - 2,
          this.sum - 1,
          this.sum
        ];
      }
    },
    secondpot: function() {
      if (this.curpage < 6 || this.curpage > this.sum - 4) {
        return false;
      } else {
        return true;
      }
    }
  }
};
</script>

<style scoped>
/* common */
#admin-edit {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: 0;
  padding-bottom: 60px;
}
#admin-edit {
  line-height: 3;
}
@media (max-width: 500px) {
  #admin-edit {
    width: 90vw;
  }
}
.control-btns {
  margin-bottom: 10px;
}
/* .add-btn{
  padding: 0;
  width: 40px;
  height: 40px;
} */

.user-list {
  width: 600px;
}

.pagi-wrapper {
  margin: 20px 0;
}
.pagi-wrapper .pagi {
  font-family: "Microsoft Ya Hei";
  font-weight: normal;
}
</style>



