<template>
  <div id="admin-login">
    <h1>admin</h1>
    <el-form
      :model="aloginForm"
      :rules="rules2"
      ref="aloginForm"
      label-width="0"
      class="demo-ruleForm"
    >
      <el-form-item prop="account">
        <el-input
          placeholder="管理员"
          type="username"
          v-model="aloginForm.account"
          auto-complete="off"
        ></el-input>
      </el-form-item>
      <el-form-item prop="passwd">
        <el-input placeholder="密码" type="password" v-model="aloginForm.passwd" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onLogin">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { get, post } from "@/helpers/request";
import md5 from "js-md5";

export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入账号"));
      } else {
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    return {
      aloginForm: {
        account: "",
        passwd: ""
      },
      rules2: {
        account: [{ validator: validatePass, trigger: "blur" }],
        passwd: [{ validator: validatePass2, trigger: "blur" }]
      }
    };
  },
  methods: {
    onLogin() {
      console.log(this.aloginForm.account, this.aloginForm.passwd);
      post("/ad/login", {
        admin_name: this.aloginForm.account,
        password: md5(this.aloginForm.passwd)
      })
      .then((res) => {
        console.log(res)
        this.$router.push({ path: this.$route.query.redirect || "/setUser" });
      });
    }
  }
};
</script>

<style scoped>
/* common */
#admin-login {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* height: 40vh; */
  /* z-index: -5; */
}
@media (max-width: 500px) {
  #admin-login {
    width: 90vw;
  }
}
#admin-login h1 {
  line-height: 3;
}

.el-form-item{
  margin-bottom: 25px;
}
</style>



